* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  padding: 0 50px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.navigation__container {
  align-items: center;
  margin: 0;
  list-style: none;
  display: flex;
}

.navigation__item {
  align-items: center;
  margin-right: 20px;
  display: flex;
}

.navigation__item:last-child {
  margin: 0;
}

.navigation__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.navigation__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  --columns-count: 1;
  grid-template-columns: repeat(var(--columns-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --columns-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --columns-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --columns-count: 4;
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card__image-block {
  justify-content: center;
  display: flex;
}

.card__image {
  object-fit: contain;
  object-position: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__product-name {
  text-align: start;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 18px;
  transition: color .3s, transform .3s;
}

.card__product-code {
  color: #616070;
  justify-content: start;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__review {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__review-count {
  text-align: center;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-tag {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

.card:hover .card__product-name {
  color: #34568b;
  cursor: pointer;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__text {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

